import React, { useState, useEffect } from "react";
import "./App.css";
import { debugData } from "../utils/debugData";
import { fetchNui } from "../utils/fetchNui";
import Toast from "../components/Toast/Toast";
import { Emotes, ActiveData, HotkeyData } from "../entity";
import { useNuiEvent } from "../hooks/useNuiEvent";

import styled, { ThemeProvider } from "styled-components";

// Define what props.theme will look like
const themeStatic = {
  fontURL: "https://fonts.googleapis.com/css?family=Athiti",
  font: "Athiti",
  PrimaryColor: "rgb(1 174 255 / 90%)",
  BackgroundColor: "rgb(0 0 0 / 60%)",
  ItemListColor: "rgb(0 0 0 / 60%)",
  Colors: {
    ['textPrimary'] : "#DDDDDD",
    ['textSecound'] : "#DDDDDD",
    ['~r~'] : "rgb(255, 0, 0)",
    ['~b~'] : "#0099ff",
    ['~g~'] : "#00ff00",
    ['~y~'] : "#ffff00",
    ['~p~'] : "#9933ff",
    ['~c~'] : "#A3A3A3",
    ['~m~'] : "#A3A3A3",
    ['~u~'] : "#000000",
    ['~o~'] : "#FF8300",
  }

};

let LiStyle = styled.li``;

let ModXStyle = styled.div`
  @import url(${(props) => `${props.theme.fontURL}`});
  font-family: ${(props) => props.theme.font} !important;
  &:hover ${LiStyle} {
    background-color: ${(props) => `${props.theme.PrimaryColor}`};
    opacity: 1;
  }
`;

let ModXDiv = styled.div`
  @import url(${(props) => `${props.theme.fontURL}`});
  font-family: ${(props) => props.theme.font} !important;
  &:hover {
    background-color: ${(props) => `${props.theme.PrimaryColor}`};
    opacity: 1;
  }
  background-color: ${(props) => `${props.theme.ItemListColor}`};
`;

let RabbitStyle = styled.div`
  @import url(${(props) => `${props.theme.fontURL}`});
  font-family: ${(props) => props.theme.font} !important;
  color: #ffffff !important;
`;

let RabbitColorStyle = styled.div`
  background: ${(props) => props.theme.BackgroundColor} !important;
`;

let RabbitItemListStyle = styled.div`
  color: #ffffff !important;
`;

// This will set the NUI to visible if we are
// developing in browser
debugData([
  {
    action: "setVisible",
    data: true,
  },
]);

debugData<HotkeyData>([
  {
    action: "setHotkeyData",
    data: {
      hotkey1: { key: "bread", label: "ขนมปัง ~r~New", type: "Emotes" },
    },
  },
]);

debugData<Emotes>([
  {
    action: "setEmotes",
    data: {
      list: [
        { key: "bread", label: "~h~~u~Ti~b~kt~r~ok ~s~ ขนมปัง ~r~New", type: "Emotes", favorite: true },
        { key: "water", label: "น้ำ", type: "Emotes", favorite: false },
        { key: "water", label: "น้ำ", type: "Emotes", favorite: false },
        { key: "water", label: "น้ำ", type: "Emotes", favorite: false },
        { key: "water", label: "น้ำ", type: "Emotes", favorite: false },
        { key: "water", label: "น้ำ", type: "Emotes", favorite: false },
        { key: "water", label: "น้ำ", type: "Emotes", favorite: false },
        { key: "water", label: "น้ำ", type: "Emotes", favorite: false },
        { key: "bread", label: "สังขยา", type: "Emotes", favorite: true },
      ],
    },
  },
]);

debugData<ActiveData>([
  {
    action: "setActiveData",
    data: {
      emoteKey: "FamilieCity",
      index: "1",
    },
  },
]);

const App: React.FC = () => {
  const [serverName, setServerName] = useState<string>("RabbitDev");
  const [theme, setTheme] = useState<any>(themeStatic);

  const [searchVaule, setSearch] = useState<string>("");

  const TextSpc = (t: string) => {
    let text = ""
    if (theme.Colors) {
      text = t.replace(/~s~/gi, `<span style="color:${theme.Colors['textSecound']}">`)
      text = text.replace(/~r~/gi, `<span style="color:${theme.Colors['~r~']}">`)
      text = text.replace(/~b~/gi, `<span style="color:${theme.Colors['~b~']}">`)
      text = text.replace(/~g~/gi, `<span style="color:${theme.Colors['~g~']}">`)
      text = text.replace(/~y~/gi, `<span style="color:${theme.Colors['~y~']}">`)
      text = text.replace(/~p~/gi, `<span style="color:${theme.Colors['~p~']}">`)
      text = text.replace(/~c~/gi, `<span style="color:${theme.Colors['~c~']}">`)
      text = text.replace(/~m~/gi, `<span style="color:${theme.Colors['~m~']}">`)
      text = text.replace(/~u~/gi, `<span style="color:${theme.Colors['~u~']}">`)
      text = text.replace(/~o~/gi, `<span style="color:${theme.Colors['~o~']}">`)
      text = text.replace(/~h~/gi, `<b>`)
    } else {
      text = t.replace(/~s~/gi, `<span style="color:white">`)
    }
    return (<div dangerouslySetInnerHTML={{ __html: `${text}`}}/>)
  }

  const [isFocus, setFocus] = useState<boolean>(true);
  const [autoClose, setAutoClose] = useState<boolean>(false);
  const [openHotkey, setOpenHotkey] = useState<boolean>(false);
  const [activeData, setActiveData] = useState<ActiveData | undefined | null>();
  const [hotkeyData, setHotkeyData] = useState<HotkeyData | undefined | null>();
  const [locales, setLocales] = useState<any>({
    ['search'] : "ค้นหา",
    ['blank'] : "ว่าง",
    ['choose'] : "เลือก",
    ['close_the_window'] : "ปิดหน้าต่างเมื่อเลือกแสดงท่าทาง",
    ['footer'] : {
        ["1"] : "Tip ! กด",
        ["2"] : "ALT ซ้าย",
        ["3"] : "เพื่อเปลี่ยนโฟกัส ระหว่างควบคุมตัวละคร หรือ หน้าต่าง Emotes | กด",
        ["4"] : "X",
        ["5"] : "Cancel Emote",
    }
  });

  const [emotes, setEmotes] = useState<Emotes | undefined | null>();
  const [emoteSearch, setEmoteSearch] = useState<Emotes | undefined | null>();

  const [hotkey, setHotkey] = useState<any>(false);
  const onSetHotkey = (key: string) => {
    setHotkey(key);
  };

  const onAutoClose = () => {
    let c = !autoClose;
    setAutoClose(c);
    fetchNui("setAutoClose", { autoClose: c });
    localStorage.setItem(serverName + ":AutoClose", JSON.stringify(c));
  };

  let toastProperties = {};

  const utilizeFocus = () => {
    const ref = React.createRef<HTMLInputElement>();
    const onFocus = () => {
      ref.current && ref.current.focus();
    };

    return { onFocus, ref };
  };

  const input1Focus = utilizeFocus();

  const [visible, setVisible] = useState(false);
  useNuiEvent<boolean>("setVisible", setVisible);
  useNuiEvent<boolean>("setAutoClose", setAutoClose);
  // Handle pressing escape/backspace
  useEffect(() => {
    // Only attach listener when we are visible
    if (!visible) return;
    onSearch("");
    setEmoteSearch(null);
    setHotkey(false);
    setOpenHotkey(false);
  }, [visible]);

  const [category, setCategory] = useState<string>("Emotes");
  useNuiEvent<Emotes>("setEmotes", (data) => {
    setEmotes(data);
  });

  useNuiEvent<HotkeyData>("setHotkeyData", (data) => {
    setHotkeyData(data);
  });

  useNuiEvent<null>("setInputFocus", () => {
    // input1Focus.onFocus()
  });

  useNuiEvent<boolean>("setFocus", (f) => {
    setFocus(f);
  });

  useNuiEvent<any>("setConfig", (c) => {
    setServerName(c.serverName);
    setTheme({
      ...theme,
      fontURL: c.fontURL,
      font: c.font,
      PrimaryColor: c.PrimaryColor,
      BackgroundColor: c.BackgroundColor,
      ItemListColor: c.ItemListColor,
      Colors: c.Colors,
    });

    ModXStyle = styled.div`
      @import url(${() => `${c.fontURL}`});
      font-family: ${() => c.font} !important;
      &:hover ${LiStyle} {
        background-color: ${() => `${c.PrimaryColor}`};
        opacity: 1;
      }
    `;
    
    ModXDiv = styled.div`
      @import url(${() => `${c.fontURL}`});
      font-family: ${() => c.font} !important;
      &:hover {
        background-color: ${() => `${c.PrimaryColor}`};
        opacity: 1;
      }
      background-color: ${() => `${c.ItemListColor}`};
    `;
    
    RabbitStyle = styled.div`
      @import url(${() => `${c.fontURL}`});
      font-family: ${() => c.font} !important;
      color: ${() => c.Colors.textPrimary} !important;
      &:li {
        color: ${() => c.Colors.textSecound} !important;
      }
    `;
    
    RabbitColorStyle = styled.div`
      background: ${() => c.BackgroundColor} !important;
    `;

    RabbitItemListStyle = styled.div`
      color: ${() => c.Colors.textSecound} !important;
    `;
  });

  useNuiEvent<any>("setLocales", (c) => {
    setLocales(c)
  });

  useEffect(() => {
  
  }, [locales]);

  useNuiEvent<ActiveData>("setActiveData", (data) => {
    setActiveData(data);
  });

  useNuiEvent("setSearch", () => {
    onSearch(searchVaule);
  });

  useNuiEvent("getLocalStorage", () => {
    const h = localStorage.getItem(serverName + ":Hotkey");
    const a = localStorage.getItem(serverName + ":AutoClose");
    const f = localStorage.getItem(serverName + ":Favorite");
    if (f !== undefined && f !== null) {
      fetchNui("setFavorite", { favorite: JSON.parse(f) });
    }
    if (h !== undefined && h !== null) {
      fetchNui("getLocalStorage", { hotkey: JSON.parse(h) });
    }
    if (a !== undefined && a !== null) {
      fetchNui("setAutoClose", { autoClose: JSON.parse(a) });
    }
  });

  useNuiEvent("setLocalStorage", (h: any) => {
    if (h !== undefined && h !== null) {
      localStorage.setItem(serverName + ":Hotkey", JSON.stringify(h));
    }
  });

  useNuiEvent("setFavoriteLocalStorage", (f: any) => {
    if (f !== undefined && f !== null) {
      localStorage.setItem(serverName + ":Favorite", JSON.stringify(f));
    }
  });

  const clearLocalStorage = () => {
    localStorage.removeItem(serverName + ":Hotkey");
    localStorage.removeItem(serverName + ":AutoClose");
    fetchNui("clearLocalStorage", {});
  };

  const onFavorite = (key: string, type: string) => {
    fetchNui("onFavorite", { key, type });
  };

  const onClickRabbitEmote = (
    e: any,
    key: string,
    idx: string,
    type: string,
    label: string
  ) => {
    if (hotkey === false) {
      setActiveData({ emoteKey: key, index: idx });
      switch (e.detail) {
        case 1:
          fetchNui("onPlayRabbitEmote", { key, type });
          break;
        case 2:
          fetchNui("onPlayRabbitEmote", { key, type });
          fetchNui("hideUI", {});
          break;
        case 3:
          fetchNui("onPlayRabbitEmote", { key, type });
          fetchNui("hideUI", {});
          break;
        default:
          return;
      }
    } else {
      fetchNui("setHotkey", { hotkey, key, type, label });
    }
  };

  const onSearch = (v: string) => {
    setSearch(v);
    if (v) {
      let myDeepCopy = JSON.parse(JSON.stringify(emotes));
      var ee = myDeepCopy.list.filter(function (creature: any) {
        if (creature.label) {
          return creature.label.toUpperCase().match(v.toUpperCase());
        }
      });
      const dd = {
        list: ee,
      };
      setEmoteSearch(dd);
    } else {
      setEmoteSearch(null);
    }
  };

  const onSetCategory = (v: string) => {
    setCategory(v);
    setActiveData({ emoteKey: "", index: "1" });
    fetchNui("setCategory", { category: v });
  };

  const clearSearch = () => {
    onSearch("");
    setEmoteSearch(null);
  };

  const onOpenHotkey = () => {
    const c = !openHotkey;
    setOpenHotkey(c);
    if (c === false) {
      setHotkey(false);
    }
  };

  const [list, setList] = useState<any>([]);
  const [position, setPosition] = useState("bottom-center");
  const [autoDeleteTime, setDeleteTime] = useState(3000);
  let [checkValue] = useState(true);
  useNuiEvent<any>(
    "toast",
    (data: { type: string; message: string; time: number }) => {
      if (data) {
        const { type, message, time } = data;
        setDeleteTime(time);
        showToast(type, message, theme.PrimaryColor);
      }
    }
  );

  useNuiEvent<any>("setPosition", (p: string) => {
    if (p) {
      setPosition(p);
    }
  });

  const showToast = (type: string, message: string, color: string) => {
    const id = Math.floor(Math.random() * 101 + 1);

    switch (type) {
      case "success":
        toastProperties = {
          id,
          title: message,
          backgroundColor: theme.PrimaryColor,
        };
        break;
      case "error":
        toastProperties = {
          id,
          title: message,
          backgroundColor: "#d9534f",
        };
        break;
      case "info":
        toastProperties = {
          id,
          title: message,
          backgroundColor: "#5bc0de",
        };
        break;
      case "warning":
        toastProperties = {
          id,
          title: message,
          backgroundColor: "#f0ad4e",
        };
        break;

      default:
        setList([]);
    }

    setList([...list, toastProperties]);
  };

  return (
    <ThemeProvider theme={theme}>
      <RabbitStyle className="wrapper-option-main">
        <RabbitColorStyle
          className={`wrapper-emote ${!isFocus && `grayscale brightness-50`}`}
        >
          <div className="wrapper-emote-title">
            <div
              onClick={() =>
                showToast(
                  "success",
                  "Rabbit Dev Copyright © 2022",
                  theme.PrimaryColor
                )
              }
              className="wrapper-emote-title-inner"
            >
              {serverName} Emotes
            </div>
            <div onClick={() => onOpenHotkey()} className="btn-action-hotkey">
              <img src={`nui://rabbit_emotes_ui/icons/Setting.png`} alt="hotkey-icon" width="30" />
            </div>
          </div>

          <div className="wrapper-filter-search">
            <div className="input-panel">
              <input
                ref={input1Focus.ref}
                type="text"
                placeholder={locales.search}
                className="form-control"
                value={searchVaule}
                onChange={(e) => {
                  onSearch(e.target.value);
                }}
              />
              <button
                onClick={() => {
                  clearSearch();
                }}
                className="button-clear"
              >
                Clear
              </button>
            </div>
            <div>
              <ul>
                <ModXStyle>
                  <LiStyle
                    onClick={() => {
                      onSetCategory("Emotes");
                    }}
                    style={{
                      backgroundColor:
                        category === "Emotes" && theme.PrimaryColor,
                    }}
                    className={`emotes-icon`}
                  >
                    <img alt="Emotes" src={`nui://rabbit_emotes_ui/icons/Emote.png`} width="26" />
                  </LiStyle>
                </ModXStyle>
                <ModXStyle>
                  <LiStyle
                    onClick={() => {
                      onSetCategory("Dance");
                    }}
                    style={{
                      backgroundColor:
                        category === "Dance" && theme.PrimaryColor,
                    }}
                    className={`emotes-icon`}
                  >
                    <img alt="Dance" src={`nui://rabbit_emotes_ui/icons/Dance.png`} width="26" />
                  </LiStyle>
                </ModXStyle>
                <ModXStyle>
                  <LiStyle
                    onClick={() => {
                      onSetCategory("PropEmotes");
                    }}
                    style={{
                      backgroundColor:
                        category === "PropEmotes" && theme.PrimaryColor,
                    }}
                    className={`emotes-icon`}
                  >
                    <img alt="PropEmotes" src={`nui://rabbit_emotes_ui/icons/Props.png`} width="26" />
                  </LiStyle>
                </ModXStyle>
                <ModXStyle>
                  <LiStyle
                    onClick={() => {
                      onSetCategory("ShareEmotes");
                    }}
                    style={{
                      backgroundColor:
                        category === "ShareEmotes" && theme.PrimaryColor,
                    }}
                    className={`emotes-icon`}
                  >
                    <img alt="ShareEmotes" src={`nui://rabbit_emotes_ui/icons/Share_Props.png`} width="26" />
                  </LiStyle>
                </ModXStyle>
                <ModXStyle>
                  <LiStyle
                    onClick={() => {
                      onSetCategory("ShareDances");
                    }}
                    style={{
                      backgroundColor:
                        category === "ShareDances" && theme.PrimaryColor,
                    }}
                    className={`emotes-icon`}
                  >
                    <img alt="ShareDances" src={`nui://rabbit_emotes_ui/icons/Share_Dance.png`} width="26" />
                  </LiStyle>
                </ModXStyle>
                <ModXStyle>
                  <LiStyle
                    onClick={() => {
                      onSetCategory("Walks");
                    }}
                    style={{
                      backgroundColor:
                        category === "Walks" && theme.PrimaryColor,
                    }}
                    className={`emotes-icon`}
                  >
                    <img alt="Walks" src={`nui://rabbit_emotes_ui/icons/Walks.png`} width="26" />
                  </LiStyle>
                </ModXStyle>
                <ModXStyle>
                  <LiStyle
                    onClick={() => {
                      onSetCategory("Moods");
                    }}
                    style={{
                      backgroundColor:
                        category === "Moods" && theme.PrimaryColor,
                    }}
                    className={`emotes-icon`}
                  >
                    <img alt="Moods" src={`nui://rabbit_emotes_ui/icons/Moods.png`} width="26" />
                  </LiStyle>
                </ModXStyle>
              </ul>
            </div>
          </div>

          <div className="wrapper-emote-content margin-section">
            <div className="wrapper-emote-content-title d-flex align-items-center justify-content-between">
              <div className="wrapper-emote-content-title-inner">
                {category}
              </div>
              <div className="wrapper-emote-content-amount">
                {activeData?.index ? activeData?.index : "1"}/
                {!emoteSearch ? emotes?.list.length : emoteSearch?.list.length}
              </div>
            </div>
            <div className="emotes-list">
              {!emoteSearch &&
                emotes &&
                emotes.list.map((em, index) => {
                  if (em.favorite) {
                    return (
                      <ModXDiv
                        key={`${index + 1}`}
                        onClick={(e) =>
                          onClickRabbitEmote(
                            e,
                            em.key,
                            `${index + 1}`,
                            em.type,
                            em.label
                          )
                        }
                        style={{
                          backgroundColor:
                            activeData?.index === `${index + 1}` &&
                            activeData?.emoteKey === `${em.key}` &&
                            theme.PrimaryColor,
                        }}
                        className={`wrapper-emote-content-inner d-flex align-items-center justify-content-between`}
                      >
                        <RabbitItemListStyle className="wrapper-emote-content-name">
                          {TextSpc(`${index + 1}. ${em.label}`)}
                        </RabbitItemListStyle>
                        {em.type !== "Moods" && em.type !== "Walks" && (
                          <div
                            onClick={() => onFavorite(em.key, em.type)}
                            className="wrapper-emote-content-icon"
                          >
                            <img
                              src={em.favorite ? `nui://rabbit_emotes_ui/icons/StarActiveYellow.png` : `nui://rabbit_emotes_ui/icons/Star.png`}
                              alt="star-icon"
                              width="20"
                            />
                          </div>
                        )}
                      </ModXDiv>
                    );
                  }
                })}
              {!emoteSearch
                ? emotes &&
                  emotes.list.map((em, index) => {
                    if (!em.favorite) {
                      return (
                        <ModXDiv
                          key={`${index + 1}`}
                          onClick={(e) =>
                            onClickRabbitEmote(
                              e,
                              em.key,
                              `${index + 1}`,
                              em.type,
                              em.label
                            )
                          }
                          style={{
                            backgroundColor:
                              activeData?.index === `${index + 1}` &&
                              activeData?.emoteKey === `${em.key}` &&
                              theme.PrimaryColor,
                          }}
                          className={`wrapper-emote-content-inner d-flex align-items-center justify-content-between`}
                        >
                          <RabbitItemListStyle className="wrapper-emote-content-name">
                            {TextSpc(`${index + 1}. ${em.label}`)}
                          </RabbitItemListStyle>
                          {em.type !== "Moods" && em.type !== "Walks" && (
                            <div
                              onClick={() => onFavorite(em.key, em.type)}
                              className="wrapper-emote-content-icon"
                            >
                              <img
                                src={em.favorite ? `nui://rabbit_emotes_ui/icons/StarActiveYellow.png` : `nui://rabbit_emotes_ui/icons/Star.png`}
                                alt="star-icon"
                                width="20"
                              />
                            </div>
                          )}
                        </ModXDiv>
                      );
                    }
                  })
                : emoteSearch &&
                  emoteSearch.list.map((em, index) => {
                    return (
                      <ModXDiv
                        key={`${index + 1}`}
                        onClick={(e) =>
                          onClickRabbitEmote(
                            e,
                            em.key,
                            `${index + 1}`,
                            em.type,
                            em.label
                          )
                        }
                        style={{
                          backgroundColor:
                            activeData?.index === `${index + 1}` &&
                            activeData?.emoteKey === `${em.key}` &&
                            theme.PrimaryColor,
                        }}
                        className={`wrapper-emote-content-inner d-flex align-items-center justify-content-between`}
                      >
                        <RabbitItemListStyle className="wrapper-emote-content-name">
                          {TextSpc(`${index + 1}. ${em.label}`)}
                        </RabbitItemListStyle>
                        {em.type !== "Moods" && em.type !== "Walks" && (
                          <div
                            onClick={() => onFavorite(em.key, em.type)}
                            className="wrapper-emote-content-icon"
                          >
                            <img
                              src={em.favorite ? `nui://rabbit_emotes_ui/icons/StarActiveYellow.png` : `nui://rabbit_emotes_ui/icons/Star.png`}
                              alt="star-icon"
                              width="20"
                            />
                          </div>
                        )}
                      </ModXDiv>
                    );
                  })}
            </div>
          </div>

          {/* <div className="wrapper-emote-content">
            <div className="wrapper-emote-content-title">
              <div className="wrapper-emote-content-title-inner">
                ผลการค้นหาอื่นๆ
              </div>
            </div>
            <div className='history-list'>
              <div className="wrapper-emote-content-inner d-flex align-items-center justify-content-between">
                <div className="wrapper-emote-content-name">Sunbathe 3</div>
                <div className="wrapper-emote-content-icon">
                  <img src={starIcon} alt="star-icon" width="20"/>
                </div>
              </div>
              <div className="wrapper-emote-content-inner d-flex align-items-center justify-content-between">
                <div className="wrapper-emote-content-name">Sunbathe 3</div>
                <div className="wrapper-emote-content-icon">
                  <img src={starIcon} alt="star-icon" width="20"/>
                </div>
              </div>
              <div className="wrapper-emote-content-inner d-flex align-items-center justify-content-between">
                <div className="wrapper-emote-content-name">Sunbathe 3</div>
                <div className="wrapper-emote-content-icon">
                  <img src={starIcon} alt="star-icon" width="20"/>
                </div>
              </div>
              <div className="wrapper-emote-content-inner d-flex align-items-center justify-content-between">
                <div className="wrapper-emote-content-name">Sunbathe 3</div>
                <div className="wrapper-emote-content-icon">
                  <img src={starIcon} alt="star-icon" width="20"/>
                </div>
              </div>
              <div className="wrapper-emote-content-inner d-flex align-items-center justify-content-between">
                <div className="wrapper-emote-content-name">Sunbathe 3</div>
                <div className="wrapper-emote-content-icon">
                  <img src={starIcon} alt="star-icon" width="20"/>
                </div>
              </div>
            </div>
          </div> */}

          {activeData?.emoteKey &&
            category !== "Moods" &&
            category !== "Walks" &&
            category !== "ShareEmotes" &&
            category !== "ShareDances" && (
              <RabbitColorStyle className="wrapper-emote-footer">
                <div>/e ({`${activeData?.emoteKey}`})</div>
              </RabbitColorStyle>
            )}
        </RabbitColorStyle>

        {openHotkey && (
          <RabbitColorStyle
            className={`wrapper-emote wrapper-hotkey ${
              !isFocus && `grayscale brightness-50`
            }`}
          >
            <div className="wrapper-emote-title">
              <div className="wrapper-emote-title-inner">Hotkey</div>
              <button onClick={() => clearLocalStorage()}>Clear</button>
            </div>

            <div className="wrapper-emote-content">
              <ModXDiv
                onClick={() => onSetHotkey("1")}
                style={{
                  backgroundColor: hotkey === "1" && theme.PrimaryColor,
                }}
                className={`wrapper-emote-content-inner d-flex align-items-center`}
              >
                <RabbitItemListStyle className="wrapper-emote-content-hotkey">Numpad 1</RabbitItemListStyle>
                <RabbitItemListStyle className="text-right">
                  {hotkey === "1"
                    ? hotkeyData?.hotkey1?.label
                      ? TextSpc(`${hotkeyData?.hotkey1?.label}`)
                      : locales.choose
                    : hotkeyData?.hotkey1?.label
                    ? TextSpc(`${hotkeyData?.hotkey1?.label}`)
                    : locales.blank}
                </RabbitItemListStyle>
              </ModXDiv>
              <ModXDiv
                onClick={() => onSetHotkey("2")}
                style={{
                  backgroundColor: hotkey === "2" && theme.PrimaryColor,
                }}
                className={`wrapper-emote-content-inner d-flex align-items-center`}
              >
                <RabbitItemListStyle className="wrapper-emote-content-hotkey">Numpad 2</RabbitItemListStyle>
                <RabbitItemListStyle className="text-right">
                  {hotkey === "2"
                    ? hotkeyData?.hotkey2?.label
                      ? TextSpc(`${hotkeyData?.hotkey2?.label}`)
                      : locales.choose
                    : hotkeyData?.hotkey2?.label
                    ? TextSpc(`${hotkeyData?.hotkey2?.label}`)
                    : locales.blank}
                </RabbitItemListStyle>
              </ModXDiv>
              <ModXDiv
                onClick={() => onSetHotkey("3")}
                style={{
                  backgroundColor: hotkey === "3" && theme.PrimaryColor,
                }}
                className={`wrapper-emote-content-inner d-flex align-items-center`}
              >
                <RabbitItemListStyle className="wrapper-emote-content-hotkey">Numpad 3</RabbitItemListStyle>
                <RabbitItemListStyle className="text-right">
                  {hotkey === "3"
                    ? hotkeyData?.hotkey3?.label
                      ? TextSpc(`${hotkeyData?.hotkey3?.label}`)
                      : locales.choose
                    : hotkeyData?.hotkey3?.label
                    ? TextSpc(`${hotkeyData?.hotkey3?.label}`)
                    : locales.blank}
                </RabbitItemListStyle>
              </ModXDiv>
              <ModXDiv
                onClick={() => onSetHotkey("4")}
                style={{
                  backgroundColor: hotkey === "4" && theme.PrimaryColor,
                }}
                className={`wrapper-emote-content-inner d-flex align-items-center`}
              >
                <RabbitItemListStyle className="wrapper-emote-content-hotkey">Numpad 4</RabbitItemListStyle>
                <RabbitItemListStyle className="text-right">
                  {hotkey === "4"
                    ? hotkeyData?.hotkey4?.label
                      ? TextSpc(`${hotkeyData?.hotkey4?.label}`)
                      : locales.choose
                    : hotkeyData?.hotkey4?.label
                    ? TextSpc(`${hotkeyData?.hotkey4?.label}`)
                    : locales.blank}
                </RabbitItemListStyle>
              </ModXDiv>
              <ModXDiv
                onClick={() => onSetHotkey("5")}
                style={{
                  backgroundColor: hotkey === "5" && theme.PrimaryColor,
                }}
                className={`wrapper-emote-content-inner d-flex align-items-center`}
              >
                <RabbitItemListStyle className="wrapper-emote-content-hotkey">Numpad 5</RabbitItemListStyle>
                <RabbitItemListStyle className="text-right">
                  {hotkey === "5"
                    ? hotkeyData?.hotkey5?.label
                      ? TextSpc(`${hotkeyData?.hotkey5?.label}`)
                      : locales.choose
                    : hotkeyData?.hotkey5?.label
                    ? TextSpc(`${hotkeyData?.hotkey5?.label}`)
                    : locales.blank}
                </RabbitItemListStyle>
              </ModXDiv>
              <ModXDiv
                onClick={() => onSetHotkey("6")}
                style={{
                  backgroundColor: hotkey === "6" && theme.PrimaryColor,
                }}
                className={`wrapper-emote-content-inner d-flex align-items-center`}
              >
                <RabbitItemListStyle className="wrapper-emote-content-hotkey">Numpad 6</RabbitItemListStyle>
                <RabbitItemListStyle className="text-right">
                  {hotkey === "6"
                    ? hotkeyData?.hotkey6?.label
                      ? TextSpc(`${hotkeyData?.hotkey6?.label}`)
                      : locales.choose
                    : hotkeyData?.hotkey6?.label
                    ? TextSpc(`${hotkeyData?.hotkey6?.label}`)
                    : locales.blank}
                </RabbitItemListStyle>
              </ModXDiv>
              <ModXDiv
                onClick={() => onSetHotkey("7")}
                style={{
                  backgroundColor: hotkey === "7" && theme.PrimaryColor,
                }}
                className={`wrapper-emote-content-inner d-flex align-items-center`}
              >
                <RabbitItemListStyle className="wrapper-emote-content-hotkey">Numpad 7</RabbitItemListStyle>
                <RabbitItemListStyle className="text-right">
                  {hotkey === "7"
                    ? hotkeyData?.hotkey7?.label
                      ? TextSpc(`${hotkeyData?.hotkey7?.label}`)
                      : locales.choose
                    : hotkeyData?.hotkey7?.label
                    ? TextSpc(`${hotkeyData?.hotkey7?.label}`)
                    : locales.blank}
                </RabbitItemListStyle>
              </ModXDiv>
              <ModXDiv
                onClick={() => onSetHotkey("8")}
                style={{
                  backgroundColor: hotkey === "8" && theme.PrimaryColor,
                }}
                className={`wrapper-emote-content-inner d-flex align-items-center`}
              >
                <RabbitItemListStyle className="wrapper-emote-content-hotkey">Numpad 8</RabbitItemListStyle>
                <RabbitItemListStyle className="text-right">
                  {hotkey === "8"
                    ? hotkeyData?.hotkey8?.label
                      ? TextSpc(`${hotkeyData?.hotkey8?.label}`)
                      : locales.choose
                    : hotkeyData?.hotkey8?.label
                    ? TextSpc(`${hotkeyData?.hotkey8?.label}`)
                    : locales.blank}
                </RabbitItemListStyle>
              </ModXDiv>
              <ModXDiv
                onClick={() => onSetHotkey("9")}
                style={{
                  backgroundColor: hotkey === "9" && theme.PrimaryColor,
                }}
                className={`wrapper-emote-content-inner d-flex align-items-center`}
              >
                <RabbitItemListStyle className="wrapper-emote-content-hotkey">Numpad 9</RabbitItemListStyle>
                <RabbitItemListStyle className="text-right">
                  {hotkey === "9"
                    ? hotkeyData?.hotkey9?.label
                      ? TextSpc(`${hotkeyData?.hotkey9?.label}`)
                      : locales.choose
                    : hotkeyData?.hotkey9?.label
                    ? TextSpc(`${hotkeyData?.hotkey9?.label}`)
                    : locales.blank}
                </RabbitItemListStyle>
              </ModXDiv>
            </div>

            <div className="flex items-center justify-center w-full mt-8">
              <label className="flex items-center cursor-pointer">
                <div className="relative">
                  <input
                    onChange={() => onAutoClose()}
                    checked={autoClose}
                    id="toogleA"
                    type="checkbox"
                    className="sr-only"
                  />
                  <div className="w-10 h-4 bg-gray-400 rounded-full shadow-inner"></div>
                  <div className="dot absolute w-6 h-6 bg-white rounded-full shadow -left-1 -top-1 transition"></div>
                </div>
                <div className="ml-3 font-medium">
                  {locales.close_the_window}
                </div>
              </label>
            </div>
          </RabbitColorStyle>
        )}
        <RabbitColorStyle className="tool-tips">
          <span>
            {`${locales.footer["1"]}`}{<span className="text-primary">{locales.footer["2"]}</span>} 
            {locales.footer["3"]}{" "}
            {<span className="text-red">X</span>} Cancel Emote
          </span>
        </RabbitColorStyle>
        <Toast
          toastList={list}
          position={position}
          autoDelete={checkValue}
          dismissTime={autoDeleteTime}
        />
      </RabbitStyle>
    </ThemeProvider>
  );
};

export default App;
