import React, {Context, createContext, useContext, useEffect, useState} from "react";
import {useNuiEvent} from "../hooks/useNuiEvent";
import {fetchNui} from "../utils/fetchNui";

import styled, { ThemeProvider } from 'styled-components';

import Toast from "../components/Toast/Toast";

const VisibilityCtx = createContext<VisibilityProviderValue | null>(null)

// Define what props.theme will look like
const themeStatic = {
  PrimaryColor: "#C90404",
};

interface VisibilityProviderValue {
  setVisible: (visible: boolean) => void
  visible: boolean
}

// This should be mounted at the top level of your application, it is currently set to
// apply a CSS visibility value. If this is non-performant, this should be customized.
export const VisibilityProvider: React.FC = ({children}) => {
  const [visible, setVisible] = useState(false)

  useNuiEvent<boolean>('setVisible', setVisible)

  // Handle pressing escape/backspace
  useEffect(() => {
    // Only attach listener when we are visible
    if (!visible) return;

    const keyHandler = (e: KeyboardEvent) => {
      if (["Escape"].includes(e.code)) {
        fetchNui("hideFrame");
      }

      if (["KeyX"].includes(e.code)) {
        fetchNui("EmoteCancel");
      }
    }

    window.addEventListener("keydown", keyHandler)

    return () => window.removeEventListener("keydown", keyHandler)
  }, [visible])

  const [theme, setTheme] = useState<any>(themeStatic);

  useNuiEvent<any>('setConfig', (c) => {
    setTheme({
      ...theme,
      fontURL: c.fontURL,
      font: c.font,
      PrimaryColor: c.PrimaryColor,
    })
  })

  let toastProperties = {};
  const [list, setList] = useState<any>([]);
  const [position, setPosition] = useState("bottom-center");
  const [autoDeleteTime, setDeleteTime] = useState(3000);
  let [checkValue] = useState(true);

  useNuiEvent<any>('ToastOnTopScreen', (data: { type: string; message: string; time: number }) => {
    if (data) {
      const { type, message, time } = data;
      setDeleteTime(time);
      showToast(type, message, theme.PrimaryColor);
    }
  })

  useNuiEvent<any>('setPosition', (p: string) => {
    if (p) {
      setPosition(p);
    }
  })

  const showToast = (type: string, message: string, color: string) => {
    const id = Math.floor(Math.random() * 101 + 1);

    switch (type) {
      case "success":
        toastProperties = {
          id,
          title: message,
          backgroundColor: theme.PrimaryColor,
        };
        break;
      case "error":
        toastProperties = {
          id,
          title: message,
          backgroundColor: "#d9534f",
        };
        break;
      case "info":
        toastProperties = {
          id,
          title: message,
          backgroundColor: "#5bc0de",
        };
        break;
      case "warning":
        toastProperties = {
          id,
          title: message,
          backgroundColor: "#f0ad4e",
        };
        break;

      default:
        setList([]);
    }

    setList([...list, toastProperties]);
  };

  return (
    <VisibilityCtx.Provider
      value={{
        visible,
        setVisible
      }}
    >
    <ThemeProvider theme={theme}>
      <div style={{ visibility: visible ? 'visible' : 'hidden', height: '100%'}}>
        {children}
      </div>
    </ThemeProvider>
    <Toast
      toastList={list}
      position={position}
      autoDelete={checkValue}
      dismissTime={autoDeleteTime}
    />
  </VisibilityCtx.Provider>)
}

export const useVisibility = () => useContext<VisibilityProviderValue>(VisibilityCtx as Context<VisibilityProviderValue>)
